// Typography
// —————————————————————————————————

//== Font-face
@font-face {
  font-family: 'Circular-Regular';
  src: url(#{$font-path}/Circular-Regular.woff2) format("woff2"),
       url(#{$font-path}/Circular-Regular.woff) format("woff");
  font-style: normal;
}
@font-face {
  font-family: 'Circular-Bold';
  src: url(#{$font-path}/Circular-Bold.woff2) format("woff2"),
       url(#{$font-path}/Circular-Bold.woff) format("woff");
  font-style: normal;
}




//== Weights
.t-cc--reg {
  font-family: 'Circular-Regular';
  font-weight: normal;
}
.t-cc--bold,
strong {
  font-family: 'Circular-Bold';
  font-weight: normal;
}


//== Utils
.t--caps { text-transform: uppercase }
.t--underline,
.t--underline--hover:hover { text-decoration: underline }


//== Basic
html,
body,
input,
button {
  font-family: 'Circular-Regular';
  font-style: normal;
  font-size: 14px;
}
